// styles
import "./App.css";
import "antd/dist/antd.min.css";
import "./assets/styles/style.css";
import "./assets/styles/utils.css";

// files
import { Layout, Result } from "antd";
import SiderBar from "./components/SiderBar";
import HeaderComponent from "./components/HeaderComponent";
import { useState } from "react";
import CalendarComponent from "./components/CalendarComponent";
import NewContracts from "./components/NewContracts";
import { Route, BrowserRouter as Router, Routes } from "react-router-dom";
import AllWorks from "./components/allWokrs/AllWorks";
import StateWork from "./components/allWokrs/StateWork";
import CameralControl from "./components/comerialControl/CameralControl";
import GeologyComerialControl from "./components/geologiaComerialControl/GeologyComerialControl";
import Workers from "./components/workers/Workers";
import WorkerInfo from "./components/workers/WorkerInfo";
import Messages from "./components/messages/Messages";

function App() {
  // 1 -> nachalnik otdel
  // 2 -> ispolnitel geolog
  // 3 -> nachalnik parti
  // 4 -> laboratoriya
  // 5 -> slujba glavnogo geologa
  const [collapsed, setCollapsed] = useState(false);
  return (
    <Layout style={{ position: "relative" }}>
      <Router>
        <SiderBar collapsed={collapsed} setCollapsed={setCollapsed} />
        <Layout>
          <HeaderComponent collapsed={collapsed} setCollapsed={setCollapsed} />
          <Layout>
            <Routes>
              <Route element={<CalendarComponent />} path="/" />
              <Route element={<NewContracts />} path="/yangi_shartnomalar" />
              <Route element={<AllWorks />} path="/barcha_ishlar" />
              <Route element={<StateWork />} path="/barcha_ishlar/holat/:id" />
              <Route element={<CameralControl />} path="/komerial_nazorat" />
              <Route
                element={<GeologyComerialControl />}
                path="/geologiya_komerial_nazorat"
              />
              <Route element={<Workers />} path="/xodimlar" />
              <Route
                element={<WorkerInfo />}
                path="/xodimlar/xodim_malumoti/:id"
              />
              <Route element={<Messages />} path="/xabarlar" />
              <Route
                element={
                  <Result
                    status={404}
                    title="404"
                    subTitle="sahifa topilmadi"
                  />
                }
                path="*"
              />
            </Routes>
          </Layout>
        </Layout>
      </Router>
    </Layout>
  );
}

export default App;
