import {
  FileOutlined,
  FileZipOutlined,
  HomeOutlined,
  MessageOutlined,
  SettingOutlined,
  TeamOutlined,
  WalletOutlined,
} from "@ant-design/icons";
import { Badge, Layout, Menu } from "antd";
import { Link } from "react-router-dom";
import useWindowDimensions from "../hooks/GeWindowDimension";
import Logo from "./Logo";

const { Sider } = Layout;

function getItem(label, key, icon, children) {
  return {
    key,
    icon,
    children,
    label,
  };
}

function getLabel(label, count, path) {
  return path ? (
    <Link
      to={path}
      style={{
        display: "flex",
        justifyContent: "space-between",
        alignItems: "center",
        paddingRight: "10px",
      }}
    >
      {label} <Badge count={count} />
    </Link>
  ) : (
    <div
      style={{
        display: "flex",
        justifyContent: "space-between",
        alignItems: "center",
        paddingRight: "10px",
      }}
    >
      {label} <Badge count={count} />
    </div>
  );
}

const SiderBar = ({ collapsed, setCollapsed }) => {
  const { width } = useWindowDimensions();
  width < 800 && setCollapsed(true);
  const items = [
    getItem(<Link to="/">Bosh sahifa</Link>, "1", <HomeOutlined />),
    getItem(
      getLabel("Yangi shartnomalar", !collapsed ? 5 : 0, "/yangi_shartnomalar"),
      "2",
      collapsed ? (
        <Badge count={5} offset={[10, 15]}>
          <FileOutlined className="white" />
        </Badge>
      ) : (
        <FileOutlined />
      )
    ),
    getItem(
      getLabel("Ishlar", !collapsed ? 10 : 0, ""),
      "3",
      collapsed ? (
        <Badge count={10} offset={[10, 15]}>
          <WalletOutlined className="white" />
        </Badge>
      ) : (
        <WalletOutlined />
      ),
      [
        getItem(getLabel("Barcha ishlar", 10, "/barcha_ishlar"), "3.1"),
        //getItem(getLabel("Ishchi dastur", 451, "/ishchi_dastur"), "3.2"),
        //getItem(getLabel("Dala nazorati", 4, "/dala_nazorati"), "3.3"),
        getItem(getLabel("Komerial nazorat", 5, "/komerial_nazorat"), "3.2"),
        getItem(
          getLabel(
            "Geologiya komerial nazorati",
            6,
            "/geologiya_komerial_nazorat"
          ),
          "3.3"
        ),
        // getItem(
        //   getLabel("Bosh geodeziya nazorati", 8, "/bosh_geodeziya_nazorati"),
        //   "3.6"
        // ),
      ]
    ),
    getItem(
      getLabel("Xodimlar", !collapsed ? 5 : 0, "/xodimlar"),
      "4",
      collapsed ? (
        <Badge count={5} offset={[10, 15]}>
          <TeamOutlined className="white" />
        </Badge>
      ) : (
        <TeamOutlined />
      )
    ),
    getItem(
      getLabel("Xabarlar", !collapsed ? 5 : 0, "/xabarlar"),
      "5",
      collapsed ? (
        <Badge count={5} offset={[10, 15]}>
          <MessageOutlined className="white" />
        </Badge>
      ) : (
        <MessageOutlined />
      )
    ),
    getItem(<Link to="/arxiv">Arxiv</Link>, "6", <FileZipOutlined />),
    getItem(
      <Link to="/sozlanmalar">Sozlanmalar</Link>,
      "7",
      <SettingOutlined />
    ),
  ];
  return (
    <Sider
      width={"320px"}
      style={{
        overflow: "auto",
        height: "100vh",
        position: "sticky",
        left: 0,
        top: 0,
        bottom: 0,
      }}
      collapsed={collapsed}
      onCollapse={(value) => width > 800 && setCollapsed(value)}
    >
      <div className="logo">
        <Logo collapsed={(width < 800 && true) || collapsed} />
      </div>
      <Menu
        defaultOpenKeys={["3"]}
        items={items}
        theme="dark"
        //selectedKeys={["1"]}
        defaultSelectedKeys={["1"]}
        mode="inline"
      ></Menu>
    </Sider>
  );
};

export default SiderBar;
